import server from '../../server.js';
export default {
	data() {
		return {
			name:'',
			phone:'',
			text:''
		}
	},
	onLoad:function(e){
		
	},
	methods: {
		go:function(){
			if(!this.text){
				uni.showToast({
					title: "必须输入遇到的问题",
					position:'bottom',
					icon:'none',
					position:'center'
				});
				return;
			}
			uni.request({
				method:'POST',
				url: 'https://api.angeli.top/user.php?type=upHelp', 
				data: {
					token:server.token,
					name:this.name,
					phone:this.phone,
					text:this.text
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.code=='1'){
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							position:'center'
						});
						setTimeout(function () {
							uni.redirectTo({
								url: '../Home/Home'
							})
						}, 1500);
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							position:'center'
						});
					}
				}
			});
			
		}
	}
}
